@import '../../variables'

.resume
  padding-bottom: 20px
  opacity: 1
  width: 708px
  height: 87.75%
  background-color: $mainbg
  border-radius: 4px
  transition: left .5s ease-in-out, opacity 1s ease-in-out
  overflow-y: scroll
  position: absolute
  left: 40%
  &-hidden
    opacity: 0
    position: absolute
    left: 25%
    transition: left .5s ease-in-out, opacity 1s ease-in-out
    z-index: -2
  &_head
    border-bottom-width: 1px
    border-bottom-style: solid
    border-image: linear-gradient(to right, $bordercolor, rgba(0, 0, 0, 0)) 1 1%
    padding: 3% 5%
    p
      font-size: 1.25rem
      font-weight: 600
      color: $whitetext
      position: relative
      span
        color: $maintext
      &:before
        content: ""
        background: linear-gradient(135deg,rgba(255, 152, 0, 0.4) 0%, rgba(255, 152, 0, 0.01) 100%)
        position: absolute
        left: -12px
        top: 3px
        width: 30px
        height: 30px
        border-radius: 100%
  &_exp-edu
    display: flex
    &-experience
      border-right-width: 1px
      border-right-style: solid
      border-image: linear-gradient(to bottom, $bordercolor, rgba(0, 0, 0, 0)) 1 1%
      width: 50%
      &-head
        border-bottom-width: 1px
        border-bottom-style: solid
        border-image: linear-gradient(to left, $bordercolor, rgba(0, 0, 0, 0)) 1 1%
        padding: 5% 0 5% 8%
        p
          font-size: 17px
          color: $whitetext
          text-transform: uppercase
          font-weight: 500
          display: flex
          align-items: center
          .exp-logo
            color: $maintext
            font-size: 1.75rem
            margin-right: .5rem
      &-work
        border-bottom-width: 1px
        border-bottom-style: solid
        border-image: linear-gradient(to right, $bordercolor, rgba(0, 0, 0, 0)) 1 1%
        padding: 8% 0 8% 8%
        height: 230px
        &-year
          font-size: 13px
          border: 1px solid $maintext
          width: 125px
          text-align: center
          color: $maintext
          border-radius: 4px
          margin-bottom: 3%
        .profession
          color: $whitetext
        .company
          color: #999
          font-size: 14px
          margin-bottom: 3%
        .work-desc
          color: #999
    &-education
      width: 50%
      &-head
        border-bottom-width: 1px
        border-bottom-style: solid
        border-image: linear-gradient(to left, $bordercolor, rgba(0, 0, 0, 0)) 1 1%
        padding: 5% 0 5% 8%
        p
          font-size: 17px
          color: $whitetext
          text-transform: uppercase
          font-weight: 500
          display: flex
          align-items: center
          .edu-logo
            color: $maintext
            font-size: 1.75rem
            margin-right: .5rem
      &-uni
        border-bottom-width: 1px
        border-bottom-style: solid
        border-image: linear-gradient(to right, $bordercolor, rgba(0, 0, 0, 0)) 1 1%
        padding: 8% 0 8% 8%
        height: 230px
        &-year
          font-size: 13px
          border: 1px solid #666
          width: 100px
          text-align: center
          color: #666
          border-radius: 4px
          margin-bottom: 3%
        .uni-name
          color: $whitetext
        .uni-city
          color: #999
          font-size: 14px
          margin-bottom: 3%
        .uni-desc
          color: #999
  &_skills
    &-head
      border-bottom-width: 1px
      border-bottom-style: solid
      border-image: linear-gradient(to right, $bordercolor, rgba(0, 0, 0, 0)) 1 1%
      padding: 3% 5%
      p
        font-size: 1.25rem
        font-weight: 600
        color: $whitetext
        position: relative
        span
          color: $maintext
        &:before
          content: ""
          background: linear-gradient(135deg,rgba(255, 152, 0, 0.4) 0%, rgba(255, 152, 0, 0.01) 100%)
          position: absolute
          left: -12px
          top: 3px
          width: 30px
          height: 30px
          border-radius: 100%
    &-list
      display: flex
      border-bottom-width: 1px
      border-bottom-style: solid
      border-image: linear-gradient(to right, $bordercolor, rgba(0, 0, 0, 0)) 1 1%
      &-coding
        width: 50%
        border-right-width: 1px
        border-right-style: solid
        border-image: linear-gradient(to bottom, $bordercolor, rgba(0, 0, 0, 0)) 1 1%
        &-head
          border-bottom-width: 1px
          border-bottom-style: solid
          border-image: linear-gradient(to left, $bordercolor, rgba(0, 0, 0, 0)) 1 1%
          padding: 8% 0 8% 8%
          p
            font-size: 17px
            font-weight: 500
            color: $whitetext
            display: flex
            align-items: center
            .design-logo
              font-size: 1.75rem
              color: $maintext
              margin-right: 1%
        &-lang
          padding: 5% 8%
          color: $whitetext
          font-size: .875rem
          width: 100%
          .coding-lang-bar-html, .coding-lang-bar-css, .coding-lang-bar-js, .coding-lang-bar-react, .coding-lang-bar-dotnet
            width: 100%
            height: 4px
            background: #3e4148
          .coding-lang-bar-html-fullness, .coding-lang-bar-css-fullness, .coding-lang-bar-js-fullness, .coding-lang-bar-react-fullness, .coding-lang-bar-dotnet-fullness
            height: 100%
            background: $maintext
          .coding-lang-bar-html-fullness, .coding-lang-bar-css-fullness
            width: 95%
          .coding-lang-bar-js-fullness
            width: 85%
          .coding-lang-bar-react-fullness
            width: 75%
          .coding-lang-bar-dotnet-fullness
            width: 40%
      &-langs
        width: 50%
        &-head
          border-bottom-width: 1px
          border-bottom-style: solid
          border-image: linear-gradient(to left, $bordercolor, rgba(0, 0, 0, 0)) 1 1%
          padding: 8% 0 8% 8%
          p
            font-size: 17px
            font-weight: 500
            color: $whitetext
            display: flex
            align-items: center
            .lang-logo
              font-size: 1.75rem
              color: $maintext
              margin-right: 1%
        &-lang
          padding: 5% 8%
          color: $whitetext
          font-size: .875rem
          width: 100%
          .language-lang-bar-aze, .language-lang-bar-tr, .language-lang-bar-ru, .language-lang-bar-en
            width: 100%
            height: 4px
            background: #3e4148
          .language-lang-bar-aze-fullness, .language-lang-bar-tr-fullness, .language-lang-bar-ru-fullness, .language-lang-bar-en-fullness
            height: 100%
            background: $maintext
          .language-lang-bar-aze-fullness, .language-lang-bar-tr-fullness
            width: 100%
          .language-lang-bar-ru-fullness
            width: 95%
          .language-lang-bar-en-fullness
            width: 90%
    &-general
      padding-bottom: 3%
      position: relative
      &-head
        border-bottom-width: 1px
        border-bottom-style: solid
        border-image: linear-gradient(to right, $bordercolor, rgba(0, 0, 0, 0)) 1 1%
        padding: 4% 0
        p
          font-size: 17px
          font-weight: 500
          color: $whitetext
          width: 100%
          display: flex
          justify-content: center
          align-items: center
          .gn-skills-logo
            font-size: 1.75rem
            color: $maintext
            margin-right: 1%
      &-list
        position: absolute
        left: 50%
        transform: translateX(-50%)
        list-style-type: none
        margin-top: 3%
        width: 40%
        li
          color: $whitetext
          display: flex
          align-items: center
          line-height: 2rem
          width: 100%
          .gn-tick-logo
            color: $maintext
            font-size: 1.25rem
            margin-right: 2%






