@import '../../variables'

.header
  background-color: $mainbg
  height: 90%
  width: 480px
  border-radius: 4px
  box-shadow: 10px 10px 15px rgb(0 0 0 / 5%)
  .navbar
    width: 70px
    height: 276px
    background-color: $mainbg
    position: absolute
    top: 12%
    left: -6.5%
    border-radius: 4px
    ul
      list-style-type: none
      height: 100%
      display: flex
      flex-direction: column
      justify-content: center
      li
        //border-bottom: 1px solid #474751
        border-bottom-width: 1px
        border-bottom-style: solid
        border-image: linear-gradient(to right, $bordercolor, rgba(0, 0, 0, 0)) 1 1%
        height: 69px
        @include centering
        a
          color: $whitetext
          text-decoration: none
          @include centering
          flex-direction: column
          gap: .5rem
          font-size: 11px
          font-weight: 500
          text-transform: uppercase
          &:hover, &:active, &:focus
            color: $maintext
          .navicon
            font-size: 20px
      li:nth-child(4n)
        border: none

