@import './variables'

@media (max-width: 767px)
  .container
    overflow: auto !important

  .mini-container
    display: block !important
    width: 100% !important
    position: static !important
  .profile
    width: 100% !important
    z-index: 0 !important
    height: 92% !important
    &_btns
      bottom: -8% !important
  .header
    width: 100% !important
    height: 100% !important
    margin-bottom: 2%
    margin-top: 8%
    .navbar
      position: fixed !important
      top: 0 !important
      left: auto !important
      width: 100% !important
      display: flex
      height: 8% !important
      z-index: 50

      ul
        flex-direction: row !important
        width: 100%
        justify-content: space-between !important

        li
          border-right: 1px solid #474751
          border-bottom: none !important
          width: 25%
          height: auto !important
          a
            color: $whitetext
            text-decoration: none
            @include centering
            flex-direction: column
            gap: .5rem
            font-size: 11px
            font-weight: 500
            text-transform: uppercase
            &:hover, &:active, &:focus
              color: $maintext
            .navicon
              font-size: 20px
  .about
    position: static !important
    width: 100% !important
    height: auto !important
    &-hidden
      display: none
    &_me
      &-more
        ul
          display: block !important
    &_services
      &-list
        width: 100%
        display: flex
        flex-wrap: wrap
        justify-content: center
        &-card
          width: 100% !important
          border: 1px solid $bordercolor
  .resume
    position: static !important
    width: 100% !important
    height: auto !important
    &-hidden
      display: none
    &_exp-edu
      display: block !important
      &-experience, &-education
        width: 100% !important
    &_skills-general-list
      width: 65% !important
  .contact
    position: static !important
    width: 100% !important
    height: auto !important
    &-hidden
      display: none
  .works
    position: static !important
    width: 100% !important
    height: auto !important
    &-hidden
      display: none