@import '../../variables'

.works
  opacity: 1
  width: 708px
  height: 87.75%
  background-color: $mainbg
  border-radius: 4px
  transition: left .5s ease-in-out, opacity 1s ease-in-out
  overflow-y: scroll
  position: absolute
  left: 40%
  &-hidden
    opacity: 0
    position: absolute
    left: 25%
    transition: left .5s ease-in-out, opacity 1s ease-in-out
    z-index: -2
  &_head
    border-bottom-width: 1px
    border-bottom-style: solid
    border-image: linear-gradient(to right, $bordercolor, rgba(0, 0, 0, 0)) 1 1%
    padding: 3% 5%
    p
      font-size: 1.25rem
      font-weight: 600
      color: $whitetext
      position: relative
      span
        color: $maintext
      &:before
        content: ""
        background: linear-gradient(135deg,rgba(255, 152, 0, 0.4) 0%, rgba(255, 152, 0, 0.01) 100%)
        position: absolute
        left: -12px
        top: 3px
        width: 30px
        height: 30px
        border-radius: 100%
  &_list
    display: flex
    justify-content: center
    gap: 5%
    height: 90%
    padding-top: 5%
    &-card
      width: 45%
      height: 30%
      text-align: center
      a
        text-decoration: none
      &-img
        height: 100%
        width: 100%
        margin-bottom: 5%
        img
          height: 100%
          width: 100%
      &-name
        color: $whitetext
        &:hover
          color: $maintext
      &-type
        color: #999