@import '../../variables'

.about
  opacity: 1
  width: 708px
  height: 87.75%
  background-color: $mainbg
  border-radius: 4px
  transition: left .5s ease-in-out, opacity 1s ease-in-out
  overflow-y: scroll
  position: absolute
  left: 40%
  &-hidden
    opacity: 0
    position: absolute
    left: 25%
    transition: left .5s ease-in-out, opacity 1s ease-in-out
    z-index: -2
  &_me
    padding-bottom: 10%
    &-head
      border-bottom-width: 1px
      border-bottom-style: solid
      border-image: linear-gradient(to right, $bordercolor, rgba(0, 0, 0, 0)) 1 1%
      padding: 3% 5%
      p
        font-size: 1.25rem
        font-weight: 600
        color: $whitetext
        position: relative
        span
          color: $maintext
        &:before
          content: ""
          background: linear-gradient(135deg,rgba(255, 152, 0, 0.4) 0%, rgba(255, 152, 0, 0.01) 100%)
          position: absolute
          left: -12px
          top: 3px
          width: 30px
          height: 30px
          border-radius: 100%
    &-desc
      color: $helptext
      padding: 5%
      text-align: justify
    &-more
      padding: 0 5%
      ul
        list-style-type: none
        display: grid
        grid-template-columns: auto auto
        gap: 13%
        li
          color: $helptext
          font-weight: 500
          span
            color: $whitetext
            text-transform: uppercase
            font-size: 0.875rem
            &:after
              content: '. . . . . . . . .'
  &_services
    width: 100%
    padding-bottom: 10%
    &-head
      border-bottom-width: 1px
      border-bottom-style: solid
      border-image: linear-gradient(to right, $bordercolor, rgba(0, 0, 0, 0)) 1 1%
      padding: 3% 5%
      p
        font-size: 1.25rem
        font-weight: 600
        color: $whitetext
        position: relative
        span
          color: $maintext
        &:before
          content: ""
          background: linear-gradient(135deg,rgba(255, 152, 0, 0.4) 0%, rgba(255, 152, 0, 0.01) 100%)
          position: absolute
          left: -12px
          top: 3px
          width: 30px
          height: 30px
          border-radius: 100%
    &-list
      width: 100%
      display: flex
      flex-wrap: wrap
      justify-content: center
      &-card
        width: 50%
        border-width: 1px
        border-style: solid
        border-image: linear-gradient(to bottom, $bordercolor, rgba(0, 0, 0, 0)) 1 1%
        padding: 5%
      .front, .design, .source
        border-top: none
      .front
        border-left: none
      .design
        border-right: none
        border-left: none

      h4
        color: $whitetext
        font-size: 1.0625rem
        font-weight: 500
        padding-top: 3%
      p
        color: $helptext
        line-height: 1.75rem
        padding-top: 3%
      .card-icon
        background: linear-gradient(135deg,rgba(255, 152, 0, 0.4) 0%, rgba(255, 152, 0, 0.01) 100%)
        width: 3.75rem
        height: 3.75rem
        border-radius: 100%
        color: $maintext
        display: grid
        place-items: center
        font-size: 2rem
  &_certificates
    padding-bottom: 5%
    &-head
      border-bottom-width: 1px
      border-bottom-style: solid
      border-image: linear-gradient(to right, $bordercolor, rgba(0, 0, 0, 0)) 1 1%
      padding: 3% 5%
      p
        font-size: 1.25rem
        font-weight: 600
        color: $whitetext
        position: relative
        span
          color: $maintext
        &:before
          content: ""
          background: linear-gradient(135deg,rgba(255, 152, 0, 0.4) 0%, rgba(255, 152, 0, 0.01) 100%)
          position: absolute
          left: -12px
          top: 3px
          width: 30px
          height: 30px
          border-radius: 100%
    &-list
      &-card
        border-bottom-width: 1px
        border-bottom-style: solid
        border-image: linear-gradient(to right, $bordercolor, rgba(0, 0, 0, 0)) 1 1%
        padding: 5%
        p
          color: $whitetext
          span
            color: $maintext
        &-img
          width: 200px
          height: 200px
          margin: 5% 0
          display: flex
          justify-content: flex-start
          img
            width: 100%
            height: 100%
            object-fit: fill
        a
          @include centering
          text-decoration: none
          width: 150px
          height: 38px
          background: none
          border: 1px solid $whitetext
          outline: none
          color: $maintext
          font-size: 1rem
          font-weight: 500
          cursor: pointer
          &:active
            transform: scale(.99)
  &_clients
    &-head
      border-bottom-width: 1px
      border-bottom-style: solid
      border-image: linear-gradient(to right, $bordercolor, rgba(0, 0, 0, 0)) 1 1%
      padding: 3% 5%
      p
        font-size: 1.25rem
        font-weight: 600
        color: $whitetext
        position: relative
        span
          color: $maintext
        &:before
          content: ""
          background: linear-gradient(135deg,rgba(255, 152, 0, 0.4) 0%, rgba(255, 152, 0, 0.01) 100%)
          position: absolute
          left: -12px
          top: 3px
          width: 30px
          height: 30px
          border-radius: 100%
    &-list
      display: flex
      &-card
        width: 25%
        display: flex
        justify-content: center
        align-items: center
        height: 160px
        border-right-width: 1px
        border-right-style: solid
        border-image: linear-gradient(to bottom, $bordercolor, rgba(0, 0, 0, 0)) 1 1%
        text-align: center
        color: $whitetext
        .client-logo
          width: 50%
          img
            object-fit: contain
            width: 100%
            height: 100%



